<template>
  <div>
  <div @click="hapus()" style="position:absolute;right:10px;top:20px;z-index:100;width:40px;height:40px;background:transparent;">

  </div>
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#000b76" fill-opacity="1" d="M0,224L0,224L180,224L180,288L360,288L360,32L540,32L540,160L720,160L720,64L900,64L900,32L1080,32L1080,288L1260,288L1260,64L1440,64L1440,0L1260,0L1260,0L1080,0L1080,0L900,0L900,0L720,0L720,0L540,0L540,0L360,0L360,0L180,0L180,0L0,0L0,0Z"></path></svg>
    <div class="card animate__animated animate__backInRight">
      <div class="card-header text-center mx-auto">
        <div v-if="this.$store.state.users.gambar!='-'">
          <img
            :src="$store.state.users.gambar"
            class="img-fluid rounded-top z-depth-1"
            alt
            style="height:140px;"
            v-viewer
          />
        </div>
        <div v-else>
          <img
            src="@/static/nofound.png"
            class="img-fluid rounded-top z-depth-1"
            alt
            style="height:140px;"
            v-viewer
          />
        </div>
      </div>
      <div class="card-body">
        <!-- <button type="button" @click="$router.push('/changepassword')" class="btn btn-sm btn-style9 ">Change Password</button> -->
      <button type="button" @click="$router.push('/kedua')" class="btn btn-sm btn-style9  float-right" style="background: rgb(34,227,23);
background: linear-gradient(349deg, rgba(34,227,23,0.5410539215686274) 0%, rgba(17,232,27,0.8603816526610644) 97%);"><span class="typcn typcn-pen"></span> TTD</button>
      <br>
        <div class="md-form form-sm">
          <span class="typcn typcn-business-card text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="nip">nip</label>
          <input type="text" id="nip" name="nip" class="form-control" required v-model="vdata.nip" />
        </div>
        <div class="md-form form-sm">
          <span class="typcn typcn-user-add text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="nama">Nama</label>
          <input
            type="text"
            id="nama"
            name="nama"
            class="form-control"
            required
            v-model="vdata.nama"
          />
        </div>
        <div class="md-form form-sm">
          <span class="typcn typcn-group text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="unit">Unit Kerja</label>
          <div class="sm-form ">
              <input type="text" id="unit_kerja" name="unit_kerja" class="form-control form-control-sm" placeholder="unit_kerja" v-model="vdata.unit_kerja" >
          </div>
           <!-- <v-select :options="units" label="nama_unit" v-model="vdata.unit"></v-select> -->
          <!-- <select class="form-control" required v-model="vdata.unit">
            <option v-for="(item, index) in units" :key="index+'unit'">{{item.nama_unit}}</option>
          </select> -->
        </div>
        <!-- <p>Atasan (Default): </p>
              <v-select :options="units2" label="nama" v-model="vdata.atasan"></v-select> -->
        <br />

        <div class="md-form form-sm">
          <span class="typcn typcn-phone text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="nohp">NO WA (optional)</label>
          <input type="text" id="nohp" name="nohp" class="form-control" v-model="vdata.nohp" />
        </div>
        <div class="md-form form-sm">
          <span class="typcn typcn-image text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="gambar">Gambar (optional)</label>
          <input type="file" @change="previewImage" accept="image/*" />
        <hr class="style2">
        </div>
         <!-- <p>Notifikasi dengan Email ? <span class="font-bold font-times"> 
          {{email ?'aktif':"tidak aktif"}}
          </span> 
          </p>
        <button type="button" class="btn btn-sm" @click="email=false;vdata.emails=false" :class="{'btn-secondary':!email}">OFF</button>
        <button type="button" class="btn btn-sm" @click="email=true;vdata.emails=true" :class="{'btn-primary':email}">ON</button> -->
        <div class="md-form form-sm" v-if="email">
          <span class="typcn typcn-mail text-lg"></span>&nbsp;&nbsp;&nbsp;
          <label for="email">Email</label>
          <input type="email" id="email" name="email" class="form-control" required v-model="vdata.emailnotif" />
        </div>
        <p class="text-red text-sm font-bold">Untuk atasan pada user ini minta set ke Admin !</p>
        <p class="text-red text-sm font-bold">Klik tombol bawah untuk set device notifikasi !</p>
      <button type="button" @click="addnotif()" style="font-size:12px;background:grey;" class="btn btn-sm btn-style9 ml-2">
        <span class="typcn typcn-bell"></span> 
      </button>
        <br>
        <br> 
        
        <br />
        <button type="button" @click="$router.go(-1)" class="btn btn-md btn-dark">
          <span class="typcn typcn-arrow-left"></span> Back
        </button>
        <button
          type="button"
          @click="next()"
          v-if="vdata.nama.length>0 && vdata.unit"
          class="btn btn-sm btn-style9 float-right"
        >
          Simpan
          <span class="typcn typcn-edit"></span>
        </button>
      </div>
    </div>
   <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320"><path fill="#000b76" fill-opacity="1" d="M0,224L0,224L180,224L180,288L360,288L360,32L540,32L540,160L720,160L720,64L900,64L900,32L1080,32L1080,288L1260,288L1260,64L1440,64L1440,320L1260,320L1260,320L1080,320L1080,320L900,320L900,320L720,320L720,320L540,320L540,320L360,320L360,320L180,320L180,320L0,320L0,320Z"></path></svg>
  </div>
</template>
<script>
import axios from "axios";
import firebase from "firebase";
let db = firebase.firestore();
export default {
  data() {
    return {
      vdata: {
        nama: "",
        unit: ""
      },
      units: [],
      units2: [],
      unit: '',
      imageData: null,
      picture: null,
      file:'',
      email: false,
      uploadValue: 0
    };
  },
  beforeMount() {
    db.collection("users")
      .doc(this.$store.state.users.uid)
      .get()
      .then(res => {
        let data = res.data();
        this.vdata.nip = data.nip;
        this.vdata.nama = data.nama;
        this.vdata.unit = data.unit;
        this.vdata.unit_kerja = data.unit_kerja;
           this.vdata.emails=this.$store.state.users.emails
    this.email=this.$store.state.users.emails
        this.vdata.emailnotif = data.emailnotif;
        this.vdata.gambar = data.gambar ? data.gambar : "";
        this.vdata.nohp = data.nohp ? data.nohp : "";
      });
  },
  layout: "lembur",
  methods: {
     addnotif(){
          // !FCM
          document.addEventListener(
            "deviceready",
            function() {
              var push = PushNotification.init({
                android: {
                  senderID: "494678481790", // setting project firebase
                  sound: "true",
                  vibrate: "true",
                  clearNotifications: true, // clear notifications from shade on app start
                  forceShow: true
                },
                ios: {
                  alert: "true",
                  badge: "true",
                  sound: "true"
                }
              });
              PushNotification.createChannel(() => {
                console.log('success');
              }, () => {
                console.log('error');
              }, {
                id: "test-channel",
                description: "My first test channel",
                importance: 5,
              vibration: [500, 1000, 500, 500],
                sound:'test', // test adalah test.wav nama file sound notification
              });
              push.on("registration", function(data) {
                // ! AMBIL data registrationId untuk ditaruh sebagai payload to : ""
                // ! simpan data regitrationId kedatabase
                localStorage.setItem("notifid", data.registrationId);
                that.regis = data.registrationId;
                alert(that.regis);
                that.$forceUpdate()
                 if(this.$store.state.users){
                  db.collection('users').doc(this.$store.state.users.uid).set({fcm:data.registrationId},{merge:true}).then(res=>{
                    alert("BERHASIL SET DEVICE INI UNTUK MENDAPATKAN NOTIFIKASI!")
                  })
                }
                // Simpan data.registrationId KE DATABASE DENGAN GANDENGAN ID USER untuk di tembak dengan payload {to : ""} firebase
              });
              push.on("notification", function(data) {});
              push.on("error", function(e) {
                console.log(e.message);
              });
            },
            false
          );
    },
    hapus(){
      let that = this;
      if(confirm("Apakah yakin menghapus akun ini ?")){
        db.collection('users').doc(this.$store.state.users.uid).set({
          hapus:'true'
        },{merge:true}).then(res=>{
          alert('berhasil hapus akun')
          that.$store.dispatch('logout');
        })
        
          
        // db.collection('users').doc(this.$store.state.users.uid).delete().then(res=>{
        //   alert("Berhasil delete 1")
        // })
      }
    },
    next() {
      db.collection("users")
        .doc(this.$store.state.users.uid)
        .set({ ...this.vdata }, { merge: true })
        .then(res => {
          alert("Simpan Berhasil")
          this.$store.dispatch('ceklogin',{true:true,false:true}).then(res=>{
            this.$router.push("/lembur");
          })
        });
    },
    previewImage(event) {
     let file = event.target.files[0]; // tambahkan data set kosong file:""
      let hasil =[]
      this.$daycaca.compress(file, 0.5, data => {
        this.$urltofile(data, file.name, file.type).then(res => {
        hasil.push(res)
      this.$nuxt.$emit('busy',true)
      setTimeout(() => {
        this.$nuxt.$emit('busy',false)
        let fd = new FormData();
        fd.append("file", hasil[0]);
        if(confirm("Apakah yakin proses upload photo ?")){
        axios
          .post("https://akbarmaliki.website/upload.php", fd, {
            headers: {
              "content-type": "multipart/form-data"
            }
          })
          .then(res => {
            this.$forceUpdate();
            console.log(res.data); // get /api/gambar/url.jpg
            this.$store.state.users.gambar = 'https://akbarmaliki.website'+res.data
            this.vdata.gambar = 'https://akbarmaliki.website'+res.data
            db.collection('users').doc(this.$store.state.users.uid).set({gambar:'https://akbarmaliki.website'+res.data},{merge:true}).then(res=>{
              alert("Berhasil di upload ! klik simpan untuk menyimpan gambar")
            })
          });
        }
      }, 2000);
        })
      })
    },
    onUpload() {
      this.picture = null;
      const storageRef = firebase
        .storage()
        .ref(`${this.imageData.name}`)
        .put(this.imageData);
      storageRef.on(
        `state_changed`,
        snapshot => {
          this.uploadValue =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        },
        error => {
          console.log(error.message);
        },
        () => {
          this.uploadValue = 100;
          storageRef.snapshot.ref.getDownloadURL().then(url => {
            this.picture = url;
            console.log("url", url);
            this.vdata.gambar = url;
          });
        }
      );
    }
  },
  mounted() {
    db.collection('users').where('bagian','==','deputy').get().then(res=>{
      let data = res.docs.map(e=>{
        return {
          uid:e.id,
          ...e.data()
        }
      })
      this.units2=data
    })
    db.collection("unit")
      .get()
      .then(res => {
        let data = res.docs.map(e => {
          return {
            id: e.id,
            ...e.data()
          };
        });
        data = _.orderBy(data, ["nama_unit"], ["asc"]);
        this.units = data;
        console.log(this.units)
      });
  }
};
</script>